<template>
  <div class="music-list">
    <!-- @click="back"-->
    <div ref="heighttopdiv">
      <div ref="backicon" class="back">
        <i class="icon-back" @click="back"></i>
      </div>
      <h1 class="title" v-html="title"></h1>
      <div class="bg-image" :style="bgStyle" ref="bgImage">
        <div class="play-wrapper">
          <!--@click="random"-->
          <div ref="playBtn" v-show="songs.length>0" class="play">
            <i class="icon-play"></i>
            <span class="text">随机播放全部</span>
          </div>
        </div>
        <div class="filter" ref="filter"></div>
      </div>
    </div>
    <div :style="offsetrelative">

      <div class="recommend2"  ref="musiclist" >
        <div class="song-list-wrapper">
          <song-list :songs="songs" :rank="rank" @select="selectItem"></song-list>
        </div>
        <!--<div v-show="!songs.length" class="loading-container">-->
          <!--<loading></loading>-->
        <!--</div>-->
      </div>

    </div>
  </div>
</template>

<script type="text/ecmascript-6">
  import BScroll from 'better-scroll'
  // import Scroll from 'base/scroll/scroll'
  import Loading from 'base/loading/loading'
  import SongList from 'base/song-list/song-list'
  // import {prefixStyle} from 'common/js/dom'
  // import {playlistMixin} from 'common/js/mixin'
  // import {mapActions} from 'vuex'
  //
  // const RESERVED_HEIGHT = 40
  // const transform = prefixStyle('transform')
  // const backdrop = prefixStyle('backdrop-filter')

  export default {
    // mixins: [playlistMixin],
    props: {
      bgImage: {
        type: String,
        default: ''
      },
      songs: {
        type: Array,
        default: []
      },
      title: {
        type: String,
        default: ''
      },
      rank: {
        type: Boolean,
        default: false
      }
    },
    data() {
      return {
        offsetrelative:{
          position: 'relative',
          "z-index": '100',
          top: '-0px',
          left: '0px',
        },
        scrollY: 0,
        topheight:0,
        backicontopheight:0,
        current_y:0
      }
    },
    computed: {
      bgStyle() {
        return `background-image:url(${this.bgImage})`
      }
    },
    // created() {
    //   this.probeType = 3
    //   this.listenScroll = true
    // },
    mounted () {
      // 百分比布局可以直接获取对象的高度和宽度
      // 通过可视区域布局
      let this_=this;
      this_.scroll = new BScroll(this_.$refs.musiclist,{probeType:2,mouseWheel: true, click: true, tap: true })
      // console.log('this_.$refs.musiclist',this_.$refs.musiclist)
      this_.scroll.refresh()
      this_.scroll.on('scroll',function (e) {
          console.log("this_.scroll:",e)
          if(!this_.topheight&&!this_.backicontopheight){
            // console.log('this_.$refs.musiclist',this_.$refs.heighttopdiv.offsetHeight)
            // console.log('this_.$refs.backicon',this_.$refs.backicon.offsetHeight)
            // 获取当前如图片的高度和图标高度
            this_.topheight=this_.$refs.heighttopdiv.offsetHeight;
            this_.backicontopheight=this_.$refs.backicon.offsetHeight;
          }else{
            // 设置定位的偏移值。。根据滚动情况
            this_.setoffsetrelative(e.y);
          }

      })
      //
    },
    // mounted() {
    //   this.imageHeight = this.$refs.bgImage.clientHeight
    //   this.minTransalteY = -this.imageHeight + RESERVED_HEIGHT
    //   this.$refs.list.$el.style.top = `${this.imageHeight}px`
    // },
    methods: {
    //   handlePlaylist(playlist) {
    //     const bottom = playlist.length > 0 ? '60px' : ''
    //     this.$refs.list.$el.style.bottom = bottom
    //     this.$refs.list.refresh()
    //   },
    //   scroll(pos) {
    //     this.scrollY = pos.y
    //   },
    //  设置滚动的偏移量，来定位滚动区域的位置
      setoffsetrelative(y){
        let this_=this;
        console.log(y)
        let m=this_.topheight-this_.backicontopheight;
        // console.log()
        // if(y<0){
        //   this_.offsetrelative.top=-m+'px';
        // }else{
        //   this_.offsetrelative.top=-2+'px';
        // }
        //  往上拉
        if((-y)<m&&y<0){
          this_.offsetrelative.top=y+'px';
          // setTimeout(function () {
          //   console.log("ssssssssssssssssssssssssssssssss")
          //   this_.scroll.refresh()
          // },1000)
        }else{
          //  往下拉
          // let top=Number(this_.offsetrelative.top.replace('px',''));
          if(y>0&&y<(m+3)){
            this_.offsetrelative.top=-y+'px';
          }
          if(y>(m+3)&&y<(m+30)){
            this_.offsetrelative.top=-2+'px';
            this_.scroll.refresh()
          }
        }

      },
    //   回到上一页
      back() {
        this.$router.back()
      },
      //  点击歌曲播放
      selectItem(item, index) {
        console.log('selectPlay',item, index)
        // this.selectPlay({
        //   list: this.songs,
        //   index
        // })
      },
      random() {
        // this.randomPlay({
        //   list: this.songs
        // })
      },
    //   ...mapActions([
    //     'selectPlay',
    //     'randomPlay'
    //   ])
    },
    // watch: {
    //   scrollY(newVal) {
    //     let translateY = Math.max(this.minTransalteY, newVal)
    //     let scale = 1
    //     let zIndex = 0
    //     let blur = 0
    //     const percent = Math.abs(newVal / this.imageHeight)
    //     if (newVal > 0) {
    //       scale = 1 + percent
    //       zIndex = 10
    //     } else {
    //       blur = Math.min(20, percent * 20)
    //     }
    //
    //     this.$refs.layer.style[transform] = `translate3d(0,${translateY}px,0)`
    //     this.$refs.filter.style[backdrop] = `blur(${blur}px)`
    //     if (newVal < this.minTransalteY) {
    //       zIndex = 10
    //       this.$refs.bgImage.style.paddingTop = 0
    //       this.$refs.bgImage.style.height = `${RESERVED_HEIGHT}px`
    //       this.$refs.playBtn.style.display = 'none'
    //     } else {
    //       this.$refs.bgImage.style.paddingTop = '70%'
    //       this.$refs.bgImage.style.height = 0
    //       this.$refs.playBtn.style.display = ''
    //     }
    //     this.$refs.bgImage.style[transform] = `scale(${scale})`
    //     this.$refs.bgImage.style.zIndex = zIndex
    //   }
    // },
    components: {
    //   Scroll,
      Loading,
      SongList
    }
  }
</script>

<style scoped lang="stylus" rel="stylesheet/stylus">
  @import "~common/stylus/variable"
  @import "~common/stylus/mixin"

  .music-list
    position: fixed
    z-index: 100
    top: 0
    left: 0
    bottom: 0
    right: 0
    background: $color-background
    .back
      position absolute
      top: 0
      left: 6px
      z-index: 50
      .icon-back
        display: block
        padding: 10px
        font-size: $font-size-large-x
        color: $color-theme
    .title
      position: absolute
      top: 0
      left: 10%
      z-index: 40
      width: 80%
      no-wrap()
      text-align: center
      line-height: 40px
      font-size: $font-size-large
      color: $color-text
    .bg-image
      position: relative
      width: 100%
      height: 0
      padding-top: 70%
      transform-origin: top
      background-size: cover
      .play-wrapper
        position: absolute
        bottom: 20px
        z-index: 50
        width: 100%
        .play
          box-sizing: border-box
          width: 135px
          padding: 7px 0
          margin: 0 auto
          text-align: center
          border: 1px solid $color-theme
          color: $color-theme
          border-radius: 100px
          font-size: 0
          .icon-play
            display: inline-block
            vertical-align: middle
            margin-right: 6px
            font-size: $font-size-medium-x
          .text
            display: inline-block
            vertical-align: middle
            font-size: $font-size-small
      .filter
        position: absolute
        top: 0
        left: 0
        width: 100%
        height: 100%
        background: rgba(7, 17, 27, 0.4)
    .bg-layer
      position: relative
      height: 100%
      background: $color-background
    .list
      position: fixed
      top: 0
      bottom: 0
      width: 100%
      background: $color-background
      .song-list-wrapper
        padding: 20px 30px
      .loading-container
        position: absolute
        width: 100%
        top: 50%
        transform: translateY(-50%)
</style>



